<template>
  <div id="app">
    <form-wizard
      id="form-wizard-1"
      @on-complete="onComplete"
      @on-loading="setLoading"
      @on-validate="handleValidation"
      @on-error="handleErrorMessage"
      ref="wizard"
      :start-index.sync="activeTabIndex"
      shape="circle"
      color="#20a0ff"
      error-color="#ff4949"
      title="Data Subject Request"
      subtitle=""
    >
      <tab-content
        title="Personal details"
        icon="el-icon-user"
        :before-change="() => validate('firstStep')"
      >
        <first-step ref="firstStep" @on-validate="onStepValidate"></first-step>
      </tab-content>
      <tab-content
        title="Verify email"
        icon="el-icon-lock"
        :before-change="() => validate('secondStep')"
        ><el-row>
          <el-col :span="24">
            <el-tag type="info"
              >Please enter the 6 digit OTP sent to your email to
              continue</el-tag
            >
            <el-divider></el-divider>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="18">
            <second-step
              ref="secondStep"
              @on-validate="onStepValidate"
            ></second-step>
          </el-col>
          <el-col :span="6">
            <el-link type="warning" @click="resendOtpRequest"
              >Resend OTP</el-link
            >
            <!-- <el-button @click="resendOtpRequest">resend OTP</el-button> -->
          </el-col>
        </el-row>
      </tab-content>
      <tab-content title="Complete" icon="el-icon-check">
        <el-alert type="success" show-icon :closable="false">
          Thank you for submiting your information. Please use the reference
          provided below to get in touch with us.
        </el-alert>
        <third-step :tmodel="finalModel"></third-step>
      </tab-content>
      <div v-if="errorMsg">
        <el-alert type="error" :description="errorMsg" show-icon> </el-alert>
        <!-- <span class="error">{{ errorMsg }}</span> -->
      </div>
      <template slot="footer" scope="props">
        <div class="wizard-footer-left">
          <wizard-button
            v-if="props.activeTabIndex > 0 && !props.isLastStep"
            :style="props.fillButtonStyle"
            :tabindex="(props.activeTabIndex + 1) * 100"
            >Previous</wizard-button
          >
        </div>
        <div class="wizard-footer-right">
          <wizard-button
            v-if="!props.isLastStep"
            @click.native="props.nextTab()"
            class="wizard-footer-right"
            :style="props.fillButtonStyle"
            :tabindex="(props.activeTabIndex + 1) * 100 + 1"
            >Next</wizard-button
          >
        </div>
      </template>
    </form-wizard>
  </div>
</template>

<script>
import axios from "axios";
import FirstStep from "./components/FirstStep.vue";
import SecondStep from "./components/SecondStep.vue";
import ThirdStep from "./components/ThirdStep.vue";
import { Loading } from "element-ui";
import util from "util";
export default {
  name: "app",
  components: {
    FirstStep,
    SecondStep,
    ThirdStep,
  },
  data() {
    return {
      finalModel: {
        email: "",
        emailReference: "",
        rtype: "",
        referrer: process.env.VUE_APP_DEFAULT_REFERRER,
        otp: "",
      },
      activeTabIndex: 0,
      errorMsg: null,
      loadingInstance: null,
      validateOtpUri: process.env.VUE_APP_OTP_VALIDATE_URI,
      requestOtpUri: process.env.VUE_APP_OTP_REQUEST_URI,
    };
  },
  methods: {
    isLastStep() {
      if (this.$refs.wizard) {
        return this.$refs.wizard.isLastStep;
      }
      return false;
    },
    onComplete() {
      alert("Yay. Done!");
    },
    forceClearError() {
      console.log("forceClearError");
      this.$refs.wizard.tabs[this.activeTabIndex].validationError = null;
    },
    validate(ref) {
      console.log("validate", ref);
      return this.$refs[ref].validate().then((valid) => {
        if (!valid) {
          return valid;
        }
        let response = null;
        switch (ref) {
          case "firstStep":
            response = this.sendOtpRequest();
            break;
          case "secondStep":
            response = this.validateOtpRequest();
            break;

          default:
            response = Promise.resolve(true);
            break;
        }
        return response;
      });
    },
    onStepValidate(validated, model) {
      console.log("onStepValidate", validated);
      if (validated) {
        this.finalModel = { ...this.finalModel, ...model };
      }
    },
    setLoading(value) {
      console.log(
        "setLoading",
        value,
        this.loadingInstance,
        new Date().toISOString()
      );
    },
    handleValidation(isValid, tabIndex) {
      console.log("handleValidation");
      console.log("Tab: " + tabIndex + " valid: " + isValid);
    },
    handleErrorMessage(errorMsg) {
      console.log("handleErrorMessage", errorMsg);
      if (errorMsg && typeof errorMsg === "object") {
        errorMsg = util.format("%s", errorMsg);
      }
      this.errorMsg = errorMsg;
    },
    sendOtpRequest() {
      // const self = this;
      console.log("sendOtpRequest....");
      console.log(JSON.stringify(this.finalModel));
      const loadingInstance = Loading.service({ target: "#form-wizard-1" });
      return axios
        .post(this.requestOtpUri, this.finalModel)
        .then((response) => {
          console.log(response);
          return Promise.resolve(true);
        })
        .catch((error) => {
          console.log(error);
          return Promise.reject(
            "Could not send OTP. Please try again in some time."
          );
        })
        .finally(() => {
          if (loadingInstance) {
            loadingInstance.close();
          }
        });
    },
    resendOtpRequest() {
      this.sendOtpRequest()
        .then(() => {
          console.log("resend OTP success");
          this.$message({
            type: "info",
            message: `Please check your email`,
          });
        })
        .catch((error) => {
          console.log("resend OTP fail!");
          this.handleErrorMessage(error);
        });
    },
    validateOtpRequest() {
      console.log("validateOtpRequest....");
      console.log(JSON.stringify(this.finalModel));
      const self = this;

      const loadingInstance = Loading.service({ target: "#form-wizard-1" });
      return axios
        .post(this.validateOtpUri, this.finalModel)
        .then((response) => {
          console.log(response);
          self.finalModel.emailReference = response.data.sid;
          return Promise.resolve(true);
        })
        .catch((error) => {
          console.error(error);
          return Promise.reject("Invalid OTP. Please try again");
        })
        .finally(() => {
          if (loadingInstance) {
            loadingInstance.close();
          }
        });
    },
  },
};
</script>

