<template>
  <el-form
    :label-position="labelPosition"
    :label-width="labelWidth"
    :model="model"
    :rules="rules"
    ref="form"
  >
    <el-form-item label="Email" prop="email">
      <el-input v-model="model.email" placeholder="Email"></el-input>
    </el-form-item>
    <el-form-item label="Request Type" prop="rtype">
      <el-select
        v-model="model.rtype"
        placeholder="Request Type"
        style="width: 100%"
      >
        <el-option
          v-for="item in options"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        >
        </el-option>
      </el-select>
    </el-form-item>
  </el-form>
</template>

<script>
import freemail from "freemail-webpack";
import util from "util";
export default {
  data() {
    return {
      labelPosition: "left",
      labelWidth: "150px",
      options: [
        {
          value: "Get Information",
          label: "Get Information",
        },
        {
          value: "Change Information",
          label: "Change Information",
        },
        {
          value: "Do Not Process",
          label: "Do Not Process",
        },
        {
          value: "Delete",
          label: "Delete",
        },
      ],
      model: {
        email: "",
        rtype: "Delete",
      },
      rules: {
        email: [
          {
            required: true,
            message: "Email is required",
            trigger: "blur",
          },
          {
            type: "email",
            message: "Invalid email",
            trigger: "change",
          },
          {
            validator: function validator(
              rule,
              value,
              callback,
              source,
              options
            ) {
              const errors = [];
              console.log(rule, value, callback, source, options);
              if (freemail.isFree(value)) {
                console.log(value + " is a free email");
                errors.push(
                  new Error(
                    util.format("%s: Please use a business email", rule.field)
                  )
                );
              }
              callback(errors);
            },
            trigger: "blur",
          },
        ],
      },
    };
  },
  methods: {
    validate() {
      console.log("FirstStep:validate");
      return new Promise((resolve) => {
        this.$refs.form.validate((valid) => {
          this.$emit("on-validate", valid, this.model);
          resolve(valid);
        });
      });
    },
  },
};
</script>

