<template>
  <el-form
    :label-position="labelPosition"
    :label-width="labelWidth"
    :model="model"
    :rules="rules"
    ref="form"
  >
    <el-form-item label="OTP" prop="otp">
      <el-input
        type="tel"
        :maxlength="maxlength"
        v-model="model.otp"
        placeholder="6 digit OTP"
      ></el-input>
    </el-form-item>
  </el-form>
</template>

<script>
import util from "util";
export default {
  data() {
    return {
      labelPosition: "left",
      labelWidth: "100px",
      maxlength: process.env.VUE_APP_OTP_MAX_LENGTH,
      model: {
        otp: null,
      },
      rules: {
        otp: [
          {
            required: true,
            message: "OTP is required",
            trigger: "blur",
          },
          {
            pattern: "^[0-9]*$",
            message: "Should be a number",
            trigger: "blur",
          },
          {
            validator: function validator(
              rule,
              value,
              callback,
              source,
              options
            ) {
              const errors = [];
              console.log(rule, value, callback, source, options);
              if (value.length < 6) {
                errors.push(
                  new Error(util.format("%s must be 6 digits", rule.field))
                );
              }

              callback(errors);
            },
            trigger: "blur",
          },
        ],
      },
    };
  },
  methods: {
    validate() {
      console.log("SecondStep:validate");
      return new Promise((resolve /* , reject */) => {
        this.$refs.form.validate((valid) => {
          this.$emit("on-validate", valid, this.model);
          resolve(valid);
        });
      });
    },
  },
};
</script>

